<template>
    <div class="barChart-container"></div>
</template>

<style>
.barChart-container{
    background-image: url("../../assets/images/横线图背景.svg");
    background-repeat: no-repeat;
    background-position: center center;
    object-fit: cover;
    padding-top: 50px;
    margin-bottom: 20px;
}
.barChart{
    width: 90vw;
    height: 85vh;
    margin-left: 5%;
    margin-top: 3%;
}
/* CSS 样式表 */
.gradient-fill {
  fill: linear-gradient(to right, red, blue);
}
</style>

<script>
    import * as d3 from 'd3';
    export default{
        mounted(){
            const svg = d3.select('.barChart-container')
                        .append("svg")
                        .attr("viewBox", "0 0 750 550")
                        .attr("width", "100%")
                        .attr("height", "100%");
            const width = 700;
            const height = 550;
            const margin = {top: 20, right: 20, bottom: 20, left: 100};
            const innerWidth = width - margin.left - margin.right;
            const innerHeight = height - margin.top - margin.bottom;
            const xValue = (datum) => {return datum.number};
            const yValue = (datum) => {return datum.bigtype};

            const render = function(data){
            // Linear Scale: Data Space -> Screen Space; 
            const xScale = d3.scaleLinear()
            .domain([0, d3.max(data, xValue)])
            .range([0, innerWidth]);

            // Introducing y-Scale; 
            const yScale = d3.scaleBand()
            .domain(data.map(yValue))
            .range([0, innerHeight])
            .padding(0.1);

            // The reason of using group is that nothing is rendered outside svg, so margin of svg is always blank while margin of group is rendered inside svg; 
            const g = svg.append('g')
            .attr('transform', `translate(${margin.left}, ${margin.top})`);
                
            // 创建一个线性渐变对象
            var gradient = svg.append("linearGradient")
            .attr("id", "myGradient")
            .attr("x1", "0%")
            .attr("y1", "0%")
            .attr("x2", "100%")
            .attr("y2", "0%");

            // 定义渐变的起始和结束颜色
            gradient.append("stop")
            .attr("offset", "0%")
            .style("stop-color", "#7400bf");

            gradient.append("stop")
            .attr("offset", "100%")
            .style("stop-color", "#ff7bac");

            // Do the data join (Enter)
            g.selectAll('rect')
            .data(data)
            .enter() 
            .append('rect')
            .attr('y', datum => yScale(yValue(datum)))
            .attr('width', (datum) => {return xScale(xValue(datum))}) 
            .attr('height', yScale.bandwidth())
            .attr('fill', 'url(#myGradient)')
            .on("mouseover",function(d,i){
                d3.select(this)
                .attr("fill","YellowGreen");
            }) 
            .on("mouseout",function(d,i){
                d3.select(this)
                .transition()
                .duration(500)
                .attr("fill","url(#myGradient)");
            }) //光标离开矩形上时填充变色

            g.selectAll("text")
            .data(data)
            .enter()
            .append("text")
            .attr("class","MyText")
            .attr("x", function(d){return 0}) //左上角
            .attr("y",function(d,i){
                return yScale(yValue(data[i]))+yScale(yValue(data[1]))*0.618
            }) //基线（文字底部）
            // .attr("dx",function(d){return xScale(xValue(d))})
            .style("fill","white")
            .style("font-size","60%")
            .text(function(d){
                return xValue(d);
            });
            // Adding axes
            g.append('g').call(d3.axisLeft(yScale)).attr('color', 'white');
            g.append('g').call(d3.axisBottom(xScale))
            .attr('transform', `translate(${0}, ${innerHeight})`).attr('color', 'white');
            }

            d3.csv('../number.csv')
            .then(function(data){
                data.forEach( datum => {
                //datum.number = parseFloat(datum.number);
                datum.number = +(datum.number);
                } )
                // console.log(data);
                // console.log(data.map( (datum) => {return datum.bigtype; } ))
                render(data);
            });
        }
    }
</script>
