<template>
	<div id="scatter"></div>
</template>

<style>
#scatter {
	width: 90%;
    height: 100%;
	/* width: 280px;
	height: 216.13px;  */
	background-color: transparent;
	margin-left: 5%;
	margin-right: 3%;
	margin-top: 3%;
	background-image: url("../../assets/images/散点图背景.svg");
	background-repeat: no-repeat;
	background-size: 100% 100%;
	background-position: center center;
}
</style>

<script type="text/javascript">
import * as d3 from "d3";
import * as echarts from 'echarts'
import axios from 'axios';
export default {
	data() {
		return {
			relation: []
		}
	},
	mounted() {
		var __this = this;
		/*var data = d3.json("../echarts_dot.json", function (csvdata) {
			return csvdata;
		}).then(function (data) {*/
			axios.get('../echarts_radar.json').then((data1) => {
			console.log(data1.data)
			let data = data1.data;
			console.log(data)
			var dom = document.getElementById('scatter');
			var myChart = echarts.init(dom, 'null', {
				renderer: 'canvas',
				useDirtyRect: false
			});
			//var app = {};

			//var option;

			console.log(data);
			var option = {
				title: {
					text: '危害程度-可触发性',
					left: 'center',
					top: 5,
					textStyle:{
						color:'white',
					}
				},
				visualMap: {
					min: 0,
					max: 10,
					dimension: 0,
					orient: 'vertical',
					top: 'center',
					//text: ['HIGH', 'LOW'],
					calculable: true,
					inRange: {
						color: ['#7400bf','#ff7bac' ,'#24b7f2']
					},
					itemWidth: 5, //图形的宽度，即长条的宽度。
					itemHeight: 140, //图形的高度，即长条的高度。
					textStyle: {
						fontSize: 14,
						fontWeight: 400,
						color:'white'
					},
					right: '0%',
					align: "right",
					//textGap:10,
					//padding: 5,
				},
				tooltip: {
					trigger: 'item',
					axisPointer: {
						type: 'cross'
					},
				},
				xAxis: [
					{
						type: 'value',
						axisLabel: {//x轴文字的配置
							show: true,
							textStyle: {
								color: "white",
							}
						},
					},
					
				],
				yAxis: [
					{
						type: 'value',
						axisLabel: {//x轴文字的配置
							show: true,
							textStyle: {
								color: "white",
							}
						},
					}
				],
				series: [
					{
						name: '危害程度',
						type: 'scatter',
						symbolSize: 5,
						data: data.data
					}
				]
			};
			myChart.setOption(option);


		})

	},
}
</script>