<template>
    <div id="radar"></div>
</template>

<style>
#radar {
    width: 90%;
    height: 90%;
    /* width: 280px;
    height: 216.13px; */
    background-color: transparent;
    margin-left: 5%;
    margin-top: 5%;
}
</style>

<script type="text/javascript">
import * as d3 from 'd3';
import axios from 'axios';
import * as echarts from 'echarts'
export default {
    data() {
        return {
            relation: []
        }
    },
    mounted() {
        var __this = this;
        /*var data = d3.json("../echarts_radar.json", function (csvdata) {
            return csvdata;
        }).then(function (data) {*/
        // axios.get('http://120.26.95.9:8081/console/overview').then((data1) => {
        axios.get('../echarts_radar.json').then((data1) => {
            console.log(data1.data)
            let data = data1.data;
            //console.log(data)
            var dom = document.getElementById('radar');
            var myChart = echarts.init(dom, 'null', {
                renderer: 'canvas',
                useDirtyRect: false
            });
            // 自定义颜色, 最多支持四个
            let colors = ['#3c4ffc','#7400bf','#ff7bac','#b337fc'];
            var length = data.data0.length;
            var num = [];
            for (var i = 0; i < length; i++) {
                num.push(data.data0[i].name);
                data.data0[i].areaStyle = {
                            type: 'default',
                            opacity: 0.25, // 图表中各个图区域的透明度
                            color: colors[i] // 图表中各个图区域的颜色
                        }
                // data.data0[i].lineStyle = {
                //             type: 'default',
                //             opacity: 0.25, // 图表中各个图区域的透明度
                //             color: colors[i] // 图表中各个图区域的颜色
                //         }
                // data.data0[i].symbolColor = colors[i]
            }
            //console.log(num);
            data.max_rate.forEach((item, index) => {
                data.max_rate[index].max = parseFloat(data.max_rate[index].max);
            })
            //console.log(data.max_rate);
            data.data0.forEach((item, index) => {
                data.data0[index].value.forEach((item, ind) => {
                    data.data0[index].value[ind] = parseFloat(data.data0[index].value[ind])
                })
            })
            console.log(data);

            var option = {
                color : ['#3c4ffc','#7400bf','#ff7bac','#b337fc'],
                title: {
                    text: '漏洞修复率',
                    left: 'left',
                    top: 10,
                    textStyle:{
                        color:"white"
                    }
                },
                tooltip: {
                    show: true,    // 是否显示提示框组件
                    trigger: 'item',    // 触发类型（'item'，数据项图形触发，主要在散点图，饼图等无类目轴的图表中使用；'axis'，坐标轴触发，主要在柱状图，折线图等会使用类目轴的图表中使用；'none'，不触发。）
                    axisPointer: {
                        type: 'cross',    // 指示器类型（'line' 直线指示器；'shadow' 阴影指示器；'none' 无指示器；'cross' 十字准星指示器。）
                        snap: false,    // 坐标轴指示器是否自动吸附到点上。默认自动判断。
                    }
                },

                legend: {
                    bottom: 4,
                    data: num,
                    textStyle: {
                        color: 'white'
                    }
                },
                radar: {
                    // shape: 'circle',
                    name:{
                        textStyle:{
                            color:'white'
                        }
                    },
                    indicator: data.max_rate,
                },
                series: [
                    {
                        name: {
                            textStyle: {
                                color: 'white'
                            }
                        },
                        type: 'radar',
                        //symbolSize: 10,
                        data: data.data0,
                        areaStyle: {
                            type: 'default'
                        },
                        textStyle: {
                            color: 'white'
                        }
                    }
                ]
            };
            myChart.setOption(option);
        })
    },
}
</script>